import { POSSystem } from "@/components/pos/Cart";
import { getServerSession } from "next-auth";
import { authOptions } from "@/lib/auth";
import { redirect } from "next/navigation";
import { LogoutButton } from "@/components/shared/LogoutButton";

export default async function POSPage() {
    const session = await getServerSession(authOptions);

    if (!session?.user) {
        redirect("/login");
    }

    return (
        <div className="min-h-screen bg-zinc-50 dark:bg-black">
            <header className="p-4 border-b border-zinc-200 dark:border-zinc-800 flex justify-between items-center bg-white dark:bg-zinc-950 sticky top-0 z-50">
                <h1 className="text-xl font-black text-primary italic">APRIL POS</h1>

                <div className="flex gap-6 items-center">
                    <div className="flex items-center gap-2 px-3 py-1 bg-primary/10 text-primary rounded-full">
                        <div className="w-2 h-2 bg-primary animate-pulse rounded-full" />
                        <span className="text-xs font-bold uppercase tracking-wider">
                            {session.user.role} Mode
                        </span>
                    </div>

                    <div className="flex items-center gap-3 border-l border-zinc-200 dark:border-zinc-800 pl-6">
                        <div className="text-right hidden sm:block">
                            <p className="text-xs font-bold text-white leading-none capitalize">{session.user.name}</p>
                            <p className="text-[10px] text-zinc-500 mt-1 uppercase tracking-tighter">Authorized Cashier</p>
                        </div>
                        <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-primary to-purple-600 flex items-center justify-center text-white font-black shadow-lg shadow-primary/20">
                            {session.user.name?.charAt(0) || "U"}
                        </div>
                        <LogoutButton />
                    </div>
                </div>
            </header>

            <POSSystem cashierId={session.user.id} />
        </div>
    );
}
